.root {
    max-width: 600px;
    flex-basis: 100%;
    margin: 30px;
    padding: 30px;
    border-radius: 20px;
    box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.1);
    background-color: #252436;
    color: white;
}

.title {
    color: white;
    margin-bottom: 30px;
}

.result {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    margin-bottom: 70px;
}

.copy {
    position: relative;
    width: 25px;
    height: 35px;
    border: 2px solid #fff;
    border-radius: 4px;
    cursor: pointer;
    z-index: 2;
    outline: none;
    background-color: transparent;
}

.copy::after {
    display: block;
    content: '';
    width: 100%;
    height: 100%;
    border: 2px solid #fff;
    position: absolute;
    bottom: -8px;
    right: -8px;
    border-radius: 4px;
    background-color: #252436;
}

.copied {
    display:block;
    width: 100%;
    font-weight: bold;
    color: white;
    text-align: center;
    position: absolute;
    bottom: -30px;
    left: 0;
    right: 0;
    animation-name: moveToTop;
    animation-duration: 0.25s;
    animation-iteration-count: 1;
}

@keyframes moveToTop {
    from {
        transform: translateY(50%);
    }
    to {
        transform: translateY(0)
    }
}

.option {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 30px;
}

.option-name {
    margin-right: 12px;
    font-weight: 600;
    color: white;
}

.option-label {
    cursor: pointer;
    margin-right: 12px;
    font-weight: 600;
    color: white;
}