.button {
    min-height: 42px;
    line-height: 42px;
    padding: 0 20px;
    background-color: #00a3ff;
    cursor: pointer;
    color: #fff;
    font-weight: 300;
    font-size: 20px;
    text-transform: uppercase;
    border: none;
    border-radius: 10px;
    outline: none;
}

.button:active {
    color: white;
    background-color: #202e48;
}